import classes from './ImageButton.module.scss';

const ImageButton = (props) => {
  return (
    <div
      className={classes['image-button']}
      style={{ 
        backgroundImage: `url('${props.image}')`,
        width: `${props.size}rem`,
        height: `${props.size}rem`
      }}
    />
  )
}

export default ImageButton;