import ImageButton from '../Buttons/ImageButton';

import classes from './Header.module.scss';
import YouTubeLogo from '../../graphic/youtubeLogo.png';
import loupIcon from '../../graphic/loup.svg';
import menuIcon from '../../graphic/menu.svg';

const Header = () => {
  return (
    <header className={classes.header}>
      <div className={classes.container}>
        <ImageButton image={YouTubeLogo} size={'2.5'}/>
        <h1>Hello Przemo</h1>
      </div>
      <div className={classes.container}>
        <ImageButton image={loupIcon} size={'1.6'}/>
        <ImageButton image={menuIcon} size={'1.3'}/>
      </div>
    </header>
  )
}

export default Header;