import { Fragment } from 'react';
import { Route, Switch } from 'react-router';
import Header from './components/Ui/Header';
import Chanel from './pages/Chanel';

const App = () => {
  return (
    <Fragment>
      <Header />
    
      <Switch>
        <Route path="/chanel/pszemo" exact>
          <Chanel />
        </Route>
      </Switch>

    </Fragment>
  );
}
  
export default App;