import ImageButton from '../../Buttons/ImageButton';
import classes from './ChanelMovie.module.scss';

import menu from '../../../graphic/menu.svg'

const ChanelMovie = (props) => {
  return (
    <div className={classes.container}>
      <div className={classes.image} style={{ backgroundImage: `url('${props.image}')` }}>
        <p>{props.length}</p>
      </div>
      <div className={classes.text}>
        <h1>{props.title}</h1>
        <p>{props.views}</p>
        <p className={classes.date}>{props.date}</p>
      </div>
      <ImageButton image={menu} size={1} />
    </div>
  )
}

export default ChanelMovie;