import SortButton from '../../Buttons/SortButton';
import classes from './ChanelSortPanel.module.scss';

const ChanelSortPanel = () => {
  return (
    <div className={classes['sort-buttons']}>
      <SortButton text={'Przesłane filmy'} />
      <SortButton text={'Data dodania (od najnowszego)'} />
    </div>
  )
}

export default ChanelSortPanel;