import ImageButton from '../../Buttons/ImageButton';
import classes from './ChanelPanel.module.scss';

import YouTubeLogo from '../../../graphic/youtubeLogo.png';

const ChanelPanel = () => {
  return (
    <section className={classes.panel}>
      <div className={classes.background}>
        <div className={classes.avatar} />
      </div>
      <h1>Hello Przemo</h1>
      <div className={classes['subscribers-container']}>
        <button className={classes['subscriber-button']}>
          <ImageButton image={YouTubeLogo} size={1.5}/>
          <h3>SUBSKRYBUJESZ</h3>
        </button>
        <p>241k subskrybentów</p>
      </div>
      <ul>
        <li>GŁÓWNA</li>
        <li className={classes['li-active']}>WIDEO</li>
        <li>PLAYLISTY</li>
      </ul>
    </section>
  )
}

export default ChanelPanel