import ChanelMovie from "../components/Chanel/ChanelMovie/ChanelMovie";
import ChanelPanel from "../components/Chanel/ChanelPanel/ChanelPanel"
import ChanelSortPanel from "../components/Chanel/ChanelSortPanel/ChanelSortPanel"

import classes from './Chanel.module.scss';

import temp1 from '../graphic/templates/tem1.jpg';
import temp2 from '../graphic/templates/tem2.jpg';
import temp3 from '../graphic/templates/tem3.jpg';
import temp4 from '../graphic/templates/tem4.jpg';
import temp5 from '../graphic/templates/tem5.jpg';
import temp6 from '../graphic/templates/tem6.jpg';

const MOVIE_LIST = [{
    image: temp1,
    length: '15:21',
    title: 'Pierwsza Akcja w Terenie *BYŁ OGIEŃ!*',
    views: '122 tyś. wyświetleń',
    date: '4 dni temu'
  }, {
    image: temp3,
    length: '11:47',
    title: 'Czym Jest EVENT LOOP i Async Await w JS',
    views: '441 tyś. wyświetleń',
    date: '6 dni temu'
  }, {
    image: temp2,
    length: '21:38',
    title: 'Koduj z GITEM - Kontrola Wersji | PORADNIK',
    views: '79 tyś. wyświetleń',
    date: '1 miesiąc temu'
  }, {
    image: temp4,
    length: '21:38',
    title: 'HISTORIA NAJLEPSZEJ PRZEGLĄDARKI',
    views: '931 tyś. wyświetleń',
    date: '1 miesiąc temu'
  }, {
    image: temp5,
    length: '9:11',
    title: 'Walnij porządnego cosole loga w JS',
    views: '164 tyś. wyświetleń',
    date: '1 miesiąc temu'
  }, {
    image: temp6,
    length: '4:20',
    title: 'Programistyczny Hufiec Pracy - PRAKTYKA',
    views: '112 tyś. wyświetleń',
    date: '2 miesiące temu'
  }
]

const Chanel = () => {
  const renderMovies = MOVIE_LIST.map(movie => (
    <ChanelMovie
      key={movie.image}
      image={movie.image}
      length={movie.length}
      title={movie.title}
      views={movie.views}
      date={movie.date}
    />
  ));

  return (
    <div className={classes.chanel}>
      <ChanelPanel />
      <ChanelSortPanel />
      {renderMovies}
      <div className={classes['lds-ring']}><div></div><div></div><div></div><div></div></div>
    </div>
  )
}

export default Chanel;