import { createSlice } from '@reduxjs/toolkit';

const toolsSlice = createSlice({
    name: 'login',
    initialState: {
        isLoggedIn: false
    },
    reducers: {
        logIn(state) {
            state.isLoggedIn = !state.isLoggedIn;
        }
    }
});

export const toolsActions = toolsSlice.actions;

export default toolsSlice;